.saturn-active,
.saturn-active:hover,
.saturn-active:active {
  color: var(--white) !important;
  border-bottom: 4px solid var(--saturn-clr);
}

.saturn-inactive {
  transition: all 0.3s;
}
.saturn-inactive:hover {
  color: var(--white);
  border-bottom: 4px solid var(--saturn-clr);
}

.saturn-active-desk,
.saturn-active-desk:hover,
.saturn-active-desk:active {
  color: var(--white) !important;
  background-color: var(--saturn-clr);
  border: 1px solid var(--saturn-clr) !important;
}
